.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.friendImageAndText {
    display: flex;
    flex-direction: row;
}

.friendImageAndText img {
    width: 50px;
    height: 50px;
    align-self: flex-end;
}

.friendText {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    background-color: #0059b3;
    border-radius: 10px 10px 10px 0;
    color: #FFFFFF;
    position: relative;
    margin-left: 15px;
}

.friendText:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 100%;
    display: block;
    width: 25px;
    height: 25px;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    background: radial-gradient(60px at top left,
    transparent 49%, #0066cc 51%) no-repeat right bottom;
}

.friendName {
    padding-left: 5px;
}

.friendMessageText p {
    word-break: break-all;
    white-space: pre-wrap;
    max-width: 500px;
    padding-right: 5px;
    padding-left: 20px;
}

.friendTime {
    margin-left: 5px;
}
